"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2,
  CalendarDays, Clock
} from "lucide-react"
import logo from "@/lib/images/image.png"
// --- 인터페이스 ---
interface Promotion {
  id: string; 
  name: string;         
  pointAmount: number; 
  totalStock: number;  
  startTime: string;   
  endTime: string;     
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
  etc: string | null;
  createdAt: string; 
}

interface PromotionStatus {
  queueSize: number;
  participantCount: number;
  winnerCount: number;
  totalStock: number;
  remainingStock: number;
}

interface AdvisorRequest {
  userId: string;
  userName: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  // --- API 경로 설정 ---
  const BASE_URL = "http://34.50.7.8:3000"
  const PROMO_API = `${BASE_URL}/v1/promotions`
  const ADMIN_USER_API = `${BASE_URL}/v1/user` 

  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)

  const [statusData, setStatusData] = useState<PromotionStatus | null>(null)
  const [isStatusOpen, setIsStatusOpen] = useState(false)
  const [targetPromoName, setTargetPromoName] = useState("")

  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)
  const itemsPerPage = 5 

  // --- 슬랙 관련 기능 ---
  const handleSlackTest = async () => {
    const token = localStorage.getItem("accessToken");
    try {
      const res = await fetch(`${PROMO_API}/test/monitoring/test-slack-only`, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.isSuccess) alert("슬랙 연동 테스트 메시지가 전송되었습니다.");
    } catch (e) { alert("슬랙 API 통신 오류"); }
  };

  const handleSendSlackReport = async () => {
    const token = localStorage.getItem("accessToken");
    if (!confirm("현재 현황 보고서를 슬랙으로 전송하시겠습니까?")) return;
    try {
      const res = await fetch(`${PROMO_API}/monitoring/report/manual`, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}` }
      });
      const data = await res.json();
      if (data.isSuccess) alert("슬랙 보고서가 전송되었습니다.");
    } catch (e) { console.log("보고서 API 통신 오류"); }
  };

  // --- 프로모션 현황 조회 ---
  const fetchPromotionStatus = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${PROMO_API}/${id}/status`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setStatusData(data.result); 
        setTargetPromoName(name); 
        setIsStatusOpen(true);
      }
    } catch (error) { console.log("서버 통신 오류"); }
  }

  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=${itemsPerPage}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content); 
        setTotalPromoPages(data.result.totalPages);
      }
    } catch (error) { console.error("로드 실패", error); } finally { setIsLoading(false); }
  }, [promoPage])

  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/pending`, {
        method: "GET",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) { setAdvisorRequests(data.result); }
    } catch (error) { console.error("심사 로드 실패", error); }
  }, [])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    if (role !== "MANAGER" && role !== "MASTER") { router.push("/login"); return; }
    setUserRole(role); setUserName(name); setIsMounted(true);
    fetchPromotions(); fetchPendingAdvisors();
  }, [router, fetchPromotions, fetchPendingAdvisors])

  const [newPromo, setNewPromo] = useState({ 
    name: "", pointAmount: "", totalStock: "", 
    startTime: "", endTime: "" 
  })

  // --- 프로모션 생성 (createdAt 추가) ---
  const handleCreatePromotion = async () => {
    const token = localStorage.getItem("accessToken")
    if (!newPromo.name || !newPromo.pointAmount || !newPromo.totalStock) return alert("필수 정보를 입력하세요.")
    try {
      const res = await fetch(PROMO_API, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({ 
          ...newPromo, 
          totalStock: Number(newPromo.totalStock), 
          pointAmount: Number(newPromo.pointAmount),
          createdAt: new Date().toISOString() // 생성 일자 추가
        })
      })
      const data = await res.json()
      if (data.isSuccess) { alert("프로모션이 생성되었습니다."); setIsPromoCreateOpen(false); fetchPromotions(); }
    } catch (error) { alert("생성 중 오류 발생"); }
  }

  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const action = currentStatus === "SCHEDULED" ? "start" : "end"
    if (!confirm(`프로모션을 ${action === "start" ? "시작" : "종료"}하시겠습니까?`)) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
    } catch (error) { alert("상태 변경 실패"); }
  }

  const handleProcessAdvisor = async (userId: string, isApprove: boolean) => {
    const token = localStorage.getItem("accessToken")
    const actionText = isApprove ? "승인" : "거절"
    if (!isApprove && !confirm("해당 신청을 거절하시겠습니까?")) return

    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/${userId}/approve`, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      
      if (data.isSuccess) {
        alert(data.message || `${actionText} 처리되었습니다.`);
        setIsDetailOpen(false); 
        fetchPendingAdvisors();
      } else {
        alert(data.message || "처리 중 오류가 발생했습니다.");
      }
    } catch (error) { 
      alert("서버 통신 오류"); 
    }
  }

  const handleLogout = () => {
    if (confirm("로그아웃 하시겠습니까?")) { localStorage.clear(); router.push("/login"); }
  }

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={120} height={32} className="h-8 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold uppercase">{userRole} ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <div className="hidden md:block text-right"><p className="text-sm font-bold leading-none">{userName}님</p></div>
            <Avatar className="h-9 w-9 border-2 border-primary/20 shadow-sm">
              <AvatarFallback className="bg-primary text-primary-foreground text-xs font-black uppercase">{userName?.charAt(0)}</AvatarFallback>
            </Avatar>
            <Button variant="ghost" size="icon" onClick={handleLogout} className="rounded-xl hover:bg-destructive/10 transition-all">
              <LogOut className="h-5 w-5" />
            </Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="flex justify-between items-end mb-8">
          <h1 className="text-3xl font-bold mb-2 tracking-tighter uppercase font-black italic">
            환영합니다, <span className="text-foreground font-bold">{userName}</span> 관리자님.
          </h1>
          <div className="flex gap-2">
            <Button variant="outline" size="sm" className="rounded-xl" onClick={handleSlackTest}>
              <Slack className="h-4 w-4 mr-2 text-[#4A154B]" /> Slack 테스트
            </Button>
            <Button size="sm" className="bg-primary rounded-xl" onClick={handleSendSlackReport}>
              <Send className="h-4 w-4 mr-2" /> 슬랙 보고서 전송
            </Button>
          </div>
        </div>

        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card className="p-6 border-border shadow-none rounded-2xl">
            <Activity className="h-5 w-5 text-primary mb-4" />
            <p className="text-xs text-muted-foreground mb-1 font-black italic uppercase tracking-tighter">System Status</p>
            <p className="text-2xl font-black">Operational</p>
          </Card>
          <Card className="p-6 border-border shadow-none rounded-2xl">
            <Zap className="h-5 w-5 text-accent mb-4" />
            <p className="text-xs text-muted-foreground mb-1 font-black italic uppercase tracking-tighter">Running Promos</p>
            <p className="text-2xl font-black">{promotions.filter(p=>p.status==="ACTIVE").length} 건</p>
          </Card>
          <Card className="p-6 border-border shadow-none rounded-2xl">
            <div className="flex items-center justify-between mb-4">
              <UserCheck className="h-5 w-5 text-orange-500" />
              <Badge variant="destructive" className="animate-pulse rounded-full px-2 text-[10px]">{advisorRequests.length}</Badge>
            </div>
            <p className="text-xs text-muted-foreground mb-1 font-black italic uppercase tracking-tighter">Pending Tasks</p>
            <p className="text-2xl font-black">
              심사 대기중 <span className="text-orange-500"> {advisorRequests.length}건</span>
            </p>
          </Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-6 items-start">
          <div className="lg:col-span-2">
            <Tabs defaultValue="promotions" className="w-full">
              <TabsList className="bg-secondary/30 mb-6 p-1 rounded-xl">
                <TabsTrigger value="promotions" className="font-bold rounded-lg px-6 uppercase italic">Promotions</TabsTrigger>
                <TabsTrigger value="advisors" className="font-bold rounded-lg px-6 uppercase italic">Advisors</TabsTrigger>
              </TabsList>

              <TabsContent value="promotions" className="mt-0">
                <Card className="p-6 border-border shadow-none flex flex-col min-h-[500px] rounded-2xl relative bg-card/50">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-bold flex items-center gap-2 font-black italic uppercase tracking-tight">
                      <Sparkles className="h-5 w-5 text-primary" /> 이벤트 현황
                    </h2>
                    <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-xl px-4 font-bold shadow-lg shadow-primary/20">
                      <Plus className="h-4 w-4 mr-1" /> 신규 등록
                    </Button>
                  </div>
                  
                  <div className="flex-1 overflow-x-auto">
                    {isLoading ? (
                      <div className="h-full flex items-center justify-center py-20"><Loader2 className="animate-spin text-primary" /></div>
                    ) : (
                      <table className="w-full text-left font-sans">
                        <thead className="text-muted-foreground text-[10px] font-black uppercase border-b border-border/50 tracking-widest">
                          <tr>
                            <th className="px-2 py-3">Promotion Info</th>
                            <th className="px-4 py-3">Period</th>
                            <th className="px-2 py-3 text-center">Status</th>
                            <th className="px-2 py-3 text-right">Action</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-border/50 text-sm font-medium">
                          {promotions.map((p) => (
                            <tr key={p.id} className="group hover:bg-secondary/10 transition-all">
                              <td className="px-2 py-5">
                                <p className="font-bold text-foreground text-base italic uppercase">{p.name}</p>
                                <div className="flex flex-col gap-1 mt-1">
                                  <p className="text-xs text-primary font-mono font-bold tracking-tighter">{p.pointAmount.toLocaleString()}P · {p.totalStock}명</p>
                                  <div className="flex items-center gap-1.5 px-2 py-0.5 bg-secondary/30 w-fit rounded-md border border-border/50">
                                    
                                    <span className="text-[10px] font-bold text-muted-foreground uppercase">생성일:</span>
                                    <span className="text-[10px] font-mono text-foreground font-bold">{p.createdAt?.split('T')[0] || "N/A"}</span>
                                  </div>
                                </div>
                              </td>
                              <td className="px-4 py-5">
                                <div className="space-y-2">
                                  <div className="flex items-center gap-2">
                                    <Badge variant="outline" className="h-5 px-1.5 text-[9px] font-black border-green-500/50 text-green-600 bg-green-50 text-center min-w-[32px] justify-center">START</Badge>
                                    <span className="text-[11px] font-mono font-bold text-foreground tracking-tight">{p.startTime.replace('T', ' ').slice(0, 16)}</span>
                                  </div>
                                  <div className="flex items-center gap-2">
                                    <Badge variant="outline" className="h-5 px-1.5 text-[9px] font-black border-destructive/30 text-destructive bg-destructive/5 text-center min-w-[32px] justify-center">END</Badge>
                                    <span className="text-[11px] font-mono font-bold text-foreground tracking-tight">{p.endTime.replace('T', ' ').slice(0, 16)}</span>
                                  </div>
                                </div>
                              </td>
                              <td className="px-2 py-5 text-center">
                                <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="rounded-md font-black uppercase text-[9px] px-2">{p.status}</Badge>
                              </td>
                              <td className="px-2 py-5 text-right flex justify-end gap-1">
                                {p.status === "SCHEDULED" && (<Button size="icon" variant="ghost" className="h-8 w-8 text-green-600" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}><Play className="h-4 w-4 fill-current" /></Button>)}
                                {p.status === "ACTIVE" && (<Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}><Square className="h-4 w-4 fill-current" /></Button>)}
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-primary" onClick={() => fetchPromotionStatus(p.id, p.name)}><Info className="h-4 w-4" /></Button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    )}
                  </div>
                  <div className="flex items-center justify-center gap-2 mt-6 pt-4 border-t border-border/50">
                    <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.max(0, p-1))} disabled={promoPage === 0}><ChevronLeft className="h-4 w-4" /></Button>
                    <span className="text-[10px] font-black px-4 italic">{promoPage + 1} / {totalPromoPages}</span>
                    <Button variant="outline" size="icon" className="h-8 w-8 rounded-lg" onClick={() => setPromoPage(p => Math.min(totalPromoPages - 1, p+1))} disabled={promoPage >= totalPromoPages - 1}><ChevronRight className="h-4 w-4" /></Button>
                  </div>
                </Card>
              </TabsContent>

              <TabsContent value="advisors" className="mt-0">
                <Card className="p-6 border-border shadow-none flex flex-col min-h-[500px] rounded-2xl bg-card/50">
                  <h2 className="text-xl font-bold mb-6 flex items-center gap-2 font-black italic uppercase">
                    <UserCheck className="h-5 w-5 text-primary" /> 전문가 신청 내역
                  </h2>
                  <div className="flex-1 flex flex-col gap-3 items-stretch">
                    {advisorRequests.length > 0 ? advisorRequests.map((req) => (
                      <div key={req.userId} className="flex items-center justify-between p-4 rounded-2xl bg-secondary/10 border border-transparent group hover:border-primary/20 hover:bg-white transition-all w-full">
                        <div className="flex items-center gap-4">
                          <Avatar className="h-10 w-10 border-2 border-background flex-shrink-0">
                            <AvatarFallback className="font-bold bg-secondary text-secondary-foreground">{req.name?.[0]}</AvatarFallback>
                          </Avatar>
                          <div className="text-left">
                            <p className="font-bold text-sm tracking-tight text-foreground">{req.name}</p>
                            <p className="text-[10px] text-muted-foreground font-mono uppercase">{req.appliedAt?.split('T')[0]}</p>
                          </div>
                        </div>
                        <Button variant="outline" size="sm" className="h-8 font-bold rounded-xl px-4 border-primary/20 hover:bg-primary hover:text-white transition-colors" onClick={() => { setSelectedRequest(req); setIsDetailOpen(true); }}>
                          <Eye className="h-3.5 w-3.5 mr-2" /> 심사
                        </Button>
                      </div>
                    )) : (
                      <div className="flex flex-col items-center justify-center py-20 text-muted-foreground italic text-xs uppercase tracking-widest opacity-50 font-black">심사 대기 내역이 없습니다.</div>
                    )}
                  </div>
                </Card>
              </TabsContent>
            </Tabs>
          </div>

          <div className="mt-0 lg:mt-[64px]"> 
            <Card className="p-6 border-border shadow-none min-h-[500px] bg-secondary/5 rounded-2xl border-dashed">
              <h2 className="text-xl font-bold mb-6 flex items-center gap-2 font-black italic uppercase"><ShieldCheck className="h-5 w-5 text-primary" /> 운영 안내</h2>
              <div className="space-y-6 text-sm text-muted-foreground font-medium font-sans">
                <div className="space-y-3 p-4 bg-background rounded-xl border border-border/50">
                  <p className="font-bold text-foreground flex items-center gap-2 uppercase text-xs italic text-primary"><span className="w-1 h-3 bg-primary rounded-full"></span>참여 현황 실시간 조회</p>
                  <p className="leading-relaxed text-[11px] font-bold italic">리스트의 <span className="text-primary">Info(i)</span> 아이콘을 클릭하면 해당 프로모션의 <span className="text-foreground">대기열(Queue), 참여자, 당첨자 수, 잔여 수량</span>을 즉시 확인할 수 있습니다.</p>
                </div>
              </div>
            </Card>
          </div>
        </div>
      </main>

      <Dialog open={isDetailOpen} onOpenChange={setIsDetailOpen}>
        <DialogContent className="sm:max-w-[380px] rounded-[1.5rem] p-6 border-none shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-lg flex items-center gap-2"><UserCheck className="h-5 w-5 text-primary" /> 전문가 신청 심사</DialogTitle></DialogHeader>
          {selectedRequest && (
            <div className="space-y-4 py-2">
              <div className="flex items-center gap-3 p-3 rounded-xl bg-secondary/20 border border-border/50 shadow-inner">
                <Avatar className="h-12 w-12 border-2 border-white shadow-sm"><AvatarFallback className="font-black bg-primary text-white">{selectedRequest.name?.[0]}</AvatarFallback></Avatar>
                <div className="overflow-hidden">
                  <p className="font-black text-sm truncate uppercase tracking-tight">{selectedRequest.name}</p>
                  <p className="text-[10px] text-muted-foreground font-mono truncate">{selectedRequest.email}</p>
                </div>
              </div>
              <div className="space-y-1.5">
                <Label className="text-[10px] font-black uppercase text-muted-foreground ml-1">Application Reason</Label>
                <div className="p-4 bg-secondary/5 border border-border/40 rounded-xl text-[11px] leading-relaxed italic font-bold max-h-[100px] overflow-y-auto">
                  "{selectedRequest.reason}"
                </div>
              </div>
              <div className="grid grid-cols-2 gap-2 pt-2">
                <Button variant="outline" className="font-black uppercase italic text-[11px] h-11 rounded-xl border-2 hover:bg-destructive/10 hover:text-destructive hover:border-destructive" onClick={() => handleProcessAdvisor(selectedRequest.userId, false)}>Reject</Button>
                <Button className="font-black uppercase italic text-[11px] h-11 rounded-xl shadow-lg shadow-primary/20" onClick={() => handleProcessAdvisor(selectedRequest.userId, true)}>Approve Access</Button>
              </div>
            </div>
          )}
          <Button variant="ghost" className="w-full text-[10px] font-black text-muted-foreground uppercase h-8 mt-1" onClick={() => setIsDetailOpen(false)}>Close Window</Button>
        </DialogContent>
      </Dialog>

      <Dialog open={isStatusOpen} onOpenChange={setIsStatusOpen}>
        <DialogContent className="sm:max-w-[450px] rounded-[2rem] p-8 border-none">
          <DialogHeader><DialogTitle className="font-black italic text-xl uppercase tracking-tighter flex items-center gap-2"><Activity className="h-6 w-6 text-primary" /> Live Monitor</DialogTitle></DialogHeader>
          {statusData && (
            <div className="grid grid-cols-2 gap-3 py-4">
              <div className="p-4 bg-secondary/20 rounded-xl border border-border/50"><p className="text-[9px] font-black uppercase text-muted-foreground">Queue</p><p className="text-xl font-black">{statusData.queueSize.toLocaleString()}</p></div>
              <div className="p-4 bg-secondary/20 rounded-xl border border-border/50"><p className="text-[9px] font-black uppercase text-muted-foreground">Participants</p><p className="text-xl font-black">{statusData.participantCount.toLocaleString()}</p></div>
              <div className="p-4 bg-primary/10 rounded-xl border border-primary/20 text-primary"><p className="text-[9px] font-black uppercase">Winners</p><p className="text-xl font-black">{statusData.winnerCount.toLocaleString()}</p></div>
              <div className="p-4 bg-secondary/20 rounded-xl border border-border/50"><p className="text-[9px] font-black uppercase text-muted-foreground">Total Stock</p><p className="text-xl font-black">{statusData.totalStock.toLocaleString()}</p></div>
              <div className="col-span-2 p-5 bg-orange-500/10 rounded-xl border border-orange-500/20 text-center"><p className="text-[10px] font-black uppercase text-orange-600">Remaining</p><p className="text-3xl font-black text-orange-600">{statusData.remainingStock.toLocaleString()}</p></div>
            </div>
          )}
          <Button className="w-full h-12 font-black rounded-xl uppercase italic tracking-widest" onClick={() => setIsStatusOpen(false)}>Close Monitor</Button>
        </DialogContent>
      </Dialog>

      <Dialog open={isPromoCreateOpen} onOpenChange={setIsPromoCreateOpen}>
        <DialogContent className="sm:max-w-[400px] rounded-[2rem] p-8 border-none shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-xl">New Campaign</DialogTitle></DialogHeader>
          <div className="grid gap-4 py-2">
            <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Name</Label><Input value={newPromo.name} onChange={(e) => setNewPromo({...newPromo, name: e.target.value})} className="bg-secondary/20 border-none h-11 rounded-xl" /></div>
            <div className="grid grid-cols-2 gap-3">
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Points</Label><Input type="number" value={newPromo.pointAmount} onChange={(e) => setNewPromo({...newPromo, pointAmount: e.target.value})} className="bg-secondary/20 border-none h-11 rounded-xl" /></div>
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Stock</Label><Input type="number" value={newPromo.totalStock} onChange={(e) => setNewPromo({...newPromo, totalStock: e.target.value})} className="bg-secondary/20 border-none h-11 rounded-xl" /></div>
            </div>
            <div className="grid grid-cols-2 gap-3">
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">Start</Label><Input type="datetime-local" value={newPromo.startTime.slice(0,16)} onChange={(e) => setNewPromo({...newPromo, startTime: e.target.value + ":00"})} className="bg-secondary/20 border-none h-11 rounded-xl text-[11px]" /></div>
              <div className="space-y-1"><Label className="text-[10px] font-black uppercase text-muted-foreground">End</Label><Input type="datetime-local" value={newPromo.endTime.slice(0,16)} onChange={(e) => setNewPromo({...newPromo, endTime: e.target.value + ":00"})} className="bg-secondary/20 border-none h-11 rounded-xl text-[11px]" /></div>
            </div>
          </div>
          <Button onClick={handleCreatePromotion} className="w-full h-14 font-black rounded-2xl uppercase italic tracking-widest shadow-xl shadow-primary/20 mt-4">Deploy Campaign</Button>
        </DialogContent>
      </Dialog>
    </div>
  )
}